.testimony {
    width: 80%;
    margin: 30px auto;
    padding: 20px;
    @media only screen and (max-width: 800px) {
        width: 95%;
    }

    @media only screen and (max-width: 400px) {
        width: 100%;
    }

    &_new {
        width: 50%;
        margin-bottom: 15px;

        @media only screen and (max-width: 970px) {
            width: 70%;
        }
    
        @media only screen and (max-width: 550px) {
            width: 100%;
        }
    }

    &_list {
        border: 1px solid gainsboro;
        border-radius: 4px;
        padding: 12px;

        &-post:not(&-post:nth-child(1)) {
            margin-top: 13px;
        }

        &-post:not(:last-child) {
            border-bottom: 1px solid gainsboro;
        }

        &-post {
            padding-bottom: 7px;

            i {
                font-size: 10px;
                color: lighten(black, 40%);
            }
        }
    }
}
